import { NgModule } from '@angular/core';
import {CommonModule} from '@angular/common';
import { GridStatusComponent } from './gridStatus/gridStatus.component';
import { Routes, RouterModule } from '@angular/router';
import { EsriMapModule } from '../esriMap/esri-map.module';
import { SharedModule } from '../../shared/shared.module';
import {PopoverModule} from 'ng2-popover';
import { Ng2SmartTableModule } from 'ng2-smart-table'; 
import {ExportToCsvService} from '../../core/services/export.service';

const routes: Routes = [
    { path: '', component: GridStatusComponent },
];

@NgModule({
    imports: [
        RouterModule.forChild(routes),
        CommonModule,
        EsriMapModule,
        SharedModule,
        PopoverModule,
         Ng2SmartTableModule 
    ],
    declarations: [GridStatusComponent],
    exports: [
        RouterModule
    ],
    providers:[ExportToCsvService]
})
export class HomeModule { }



