import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { ExportToCsvService } from '../../../core/services/export.service';
import { AuthenticationService } from '../../../core/authentication/authentication.service';

declare var tableau: any;

declare var $: any;


@Component({
    selector: 'app-gridStatus',
    templateUrl: './gridStatus.component.html',
    styleUrls: ['./gridStatus.component.scss']
})
export class GridStatusComponent implements OnInit {

    tableauViz: any;
    tFeedersViz: any;
    tGridStatusViz: any;
    tSummaryViz: any;
    tCompletionViz: any;

    totalFeeders: number;
    delivered: number;
    backOffice: number;
    fcInProgress: number;
    fcToStart: string;

    totalFeedersArray: Array<any>;
    deliveredArray: Array<any>;
    backOfficeArray: Array<any>;
    fcInProgressArray: Array<any>;

    itemsTotal = [];
    itemsDelivered = [];
    itemsBackOffice = [];
    itemsInProgress = [];

    gridStatusCollapBtnClass: string = 'fa fa-minus';

    fullScreenButtons: string[] = ['fa fa-expand', 'fa fa-compress'];
    expandButtonClass = this.fullScreenButtons[0];

    kpiDiv = false;

    fullScreenTitle = 'Full Screen';

    settings: any = {
        columns: {
            id: {
                title: 'Feeders Selected'
            }
        },
        attr: {
            class: "smart-size"
        },
        pager: {
            display: true,
            perPage: 10
        },
        actions: {
            add: false,
            edit: false,
            delete: false
        }
    };

    onExpandCollapseClick(classToChange: string): void {

        this.gridStatusCollapBtnClass = this.gridStatusCollapBtnClass === 'fa fa-minus' ? 'fa fa-plus' : 'fa fa-minus';

    }

    toggleFullScreen(elementId, headerId, divId) {

        var elem = $('#' + elementId), headerElement = $('#' + headerId), divPanel = $('#' + divId), bodyHeight = $('body')[0].clientHeight;

        if (headerElement.hasClass('panelDefault')) {
            headerElement.removeClass("panelDefault");
            $('.layout-fixed .wrapper .topnavbar-wrapper').css('z-index', 112);
            this.expandButtonClass = this.fullScreenButtons[0];
            this.fullScreenTitle = 'Exit Full Screen';
        }
        else {
            headerElement.addClass("panelDefault");
            $('.layout-fixed .wrapper .topnavbar-wrapper').css('z-index', 101);
            this.expandButtonClass = this.fullScreenButtons[1];
            this.fullScreenTitle = 'Full Screen';
        }
        if (divPanel.hasClass('panel-fullscreen')) {
            divPanel.removeClass("panel-fullscreen");
            $('body').css('overflow-y', 'auto');
        }
        else {
            divPanel.addClass("panel-fullscreen");
            $('body').css('overflow-y', 'hidden');
        }
        if (elem[0].clientHeight + headerElement[0].clientHeight < bodyHeight - 20) {
            elem[0].children[0].style.height = ((bodyHeight - headerElement[0].clientHeight - 20) + 'px');
        }
        else {
            elem[0].children[0].style.height = '77.5vh';
        }
    }

    constructor(public exportToCsvService: ExportToCsvService, public authenticationService: AuthenticationService) {
        this.totalFeedersArray = [];
        this.deliveredArray = [];
        this.backOfficeArray = [];
        this.fcInProgressArray = [];
    }

    excel(typeOfHeader) {
        const title = 'Feeders';
        if (typeOfHeader == 'inProgress') {
            this.exportToCsvService.exportFunction(this.itemsInProgress, title);
        }
        if (typeOfHeader == 'backOffice') {
            this.exportToCsvService.exportFunction(this.itemsBackOffice, title);
        }
        if (typeOfHeader == 'delivered') {
            this.exportToCsvService.exportFunction(this.itemsDelivered, title);
        }
        if (typeOfHeader == 'total') {
            this.exportToCsvService.exportFunction(this.itemsTotal, title);
        }

    }



    getKpiData() {
        var sheet = this.tFeedersViz.getWorkbook().getActiveSheet(),
            // If the active sheet is not a dashboard, then you can just enter:
            // viz.getWorkbook().getActiveSheet();
            options = {
                maxRows: 0, // Max rows to return. Use 0 to return all rows
                ignoreAliases: false,
                ignoreSelection: true,
                includeAllColumns: false
            };

        sheet.getUnderlyingDataAsync(options).then(((t) => {
            this.authenticationService.getTableauToken();
            t.getData().forEach(item => {
                if (item[0].value.includes("total")) {
                    this.totalFeedersArray.push({ "id": item[0].value.split('_')[0] });
                }
                if (item[0].value.includes("delivered")) {
                    this.deliveredArray.push({ "id": item[0].value.split('_')[0] });
                }
                if (item[0].value.includes("backoffice")) {
                    this.backOfficeArray.push({ "id": item[0].value.split('_')[0] });
                }
                if (item[0].value.includes("inprogress")) {
                    this.fcInProgressArray.push({ "id": item[0].value.split('_')[0] });
                }

            });



            this.itemsTotal = this.totalFeedersArray;
            this.itemsDelivered = this.deliveredArray;
            this.itemsBackOffice = this.backOfficeArray;
            this.itemsInProgress = this.fcInProgressArray;


            this.totalFeeders = this.totalFeedersArray.length;
            this.delivered = this.deliveredArray.length;
            this.backOffice = this.backOfficeArray.length;
            this.fcInProgress = this.fcInProgressArray.length;


        }));
    }

    ngOnInit() {

        $.ajax({
            url: 'https://apigateway.cyient-fiops.com/tableauSecurity',
            type: 'POST',
            data: {
                username: 'Utilities_NAM'
            },
            xhrFields: {
                withCredentials: false
            },
            headers: {
                "Content-Type": "application/x-www-form-urlencoded"
            },
            success: function (data) {
                var placeholderDiv = document.getElementById('t-viz');

                var completionUrl = 'https://busrep.cyient.com/trusted/' + data + '/views/LandingPage/Dashboard1?:embed=y&:showAppBanner=false&:showShareOptions=true&:display_count=no&:showVizHome=no';
                //var url = 'https://reporting.cyient.com/t/UGNAMPOC/views/GridHistory/Dashboard1?:embed=y&:showAppBanner=false&:showShareOptions=true&:display_count=no&:showVizHome=no&:toolbar=no';
                var options = {
                    hideTabs: true,
                    width: '100%',
                    height: '77.5vh',
                    onFirstInteractive: (() => {

                    })

                };
                this.tableauViz = new tableau.Viz(placeholderDiv, completionUrl, options);
            },
            error:
            function (data) {
                return data;
            }

        });
        this.getKPIdata();
    }



    getKPIdata(): void {
        this.authenticationService.getTableauToken();
        var containerDiv = document.getElementById('Div1');
        var testGetDataURL = 'https://busrep.cyient.com/trusted/' + sessionStorage.getItem('tableauTokenId') + '/views/KPINEW/kpiData';
        this.tFeedersViz = new tableau.Viz(containerDiv, testGetDataURL, {
            hideTabs: true,
            width: 0,
            height: 0,
            onFirstInteractive: (() => {
                this.getKpiData();
            })
        });


    }

    ngOnDestroy(): void {
        this.tableauViz != undefined ? this.tableauViz.dispose() : "";
        this.tFeedersViz != undefined ? this.tFeedersViz.dispose() : "";

    }

}



